import React, { useEffect, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useDropzone } from "react-dropzone";
import "./FileUploader.scss";
import {
  templateDownload,
  csvReader,
  putCsvFieldJson,
  validateColumn,
  xlsReader,
  cleaningData,
  resetCsvResist,
} from "./services.js";
import FileUploadProgress from "Component/FileUploadProgress";
import PopUp from "Component/Modal/PopUp";
import { validatePoSkuInfoId, validateSkuNumber } from "apiServices";
import { getWarehouseArea, getOutboundType } from "apiServices/dropdown";

const baseStyle = {
  flex: 1,
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  paddingTop: "20px",
  paddingBottom: "20px",
  paddingLeft: "20px",
  paddingRight: "20px",
  outline: "none",
  transition: "border .24s ease-in-out",
  height: "400px",
};

const parentStyle = {
  paddingTop: "20px",
  paddingBottom: "10px",
  paddingLeft: "20px",
  paddingRight: "20px",
  borderWidth: 2,
  borderRadius: 2,
  borderColor: "#eeeeee",
  borderStyle: "dashed",
  backgroundColor: "#fff",
  outline: "none",
  transition: "border .24s ease-in-out",
  height: "600px",
};

const activeStyle = {
  borderColor: "#2196f3",
};

const acceptStyle = {
  borderColor: "#00e676",
};

const rejectStyle = {
  borderColor: "#ff1744",
};

const FileUploader = ({ field, transaction, nextPage = null, type = null }) => {
  const [isProgressDisplay, setIsProgressDisplay] = React.useState(false);
  const [isProgressStatus, setIsProgressStatus] = React.useState();
  const [isProgressPercent, setIsProgressPercent] = React.useState(0);
  const [isProgressFileName, setIsProgressFileName] = React.useState(null);
  const [isWarehouseArea, setIsWarehouseArea] = React.useState(null);
  const [isAdditionalOutbound, setIsAdditionalOutbound] = React.useState(null);
  const [isJsonArray, setIsJsonArray] = React.useState(null);
  const [isUploadStatus, setIsUploadStatus] = React.useState(null);
  const [isCheckingSchema, setIsCheckingSchema] = React.useState(null);
  const [isResetCsvPersist, setIsResetCsvPersist] = React.useState(false);
  const [defaultValues, setDefaultValues] = React.useState(0);
  const [modalShow, setModalShow] = React.useState(false);
  const [modalShow2, setModalShow2] = React.useState(false);

  //declare redux
  const dispatch = useDispatch();
  const selectorWarehouseArea = useSelector((state) => state.warehouseAreaCsv);
  const selectorOutboundType = useSelector((state) => state.outboundTypeCsv);
  const selectorStatus = useSelector((state) => state.status);
  const uploadStatus = useSelector((state) => state.uploadStatus);
  const selectorDistinctPOSKUInfoId = useSelector(
    (state) => state.csvDistinctPOSKUInfoId
  );
  const selectorDistinctSKUNumber = useSelector(
    (state) => state.csvDistinctSKUNumber
  );
  const selectorSkuCodeValidated = useSelector(
    (state) => state.skuCodeValidated
  );
  const selectorSkuNumberValidated = useSelector(
    (state) => state.skuNumberValidated
  );
  const selectorCsvData = useSelector((state) => state.csvData);

  //reset csv persist
  if (isResetCsvPersist !== true) {
    resetCsvResist({
      dispatch,
      resetStatus: isResetCsvPersist,
      setIsResetCsvPersist,
    });
  }

  //get warehouse & warehouse area data
  const warehouseUuid = useSelector((state) => state.user.warehouseUuid);
  if (isWarehouseArea === null) {
    getWarehouseArea({
      warehouseUuid: warehouseUuid,
      dispatch,
      type: "csvValidator",
    });
    setIsWarehouseArea(1);
  }

  //get additional outbound type
  if (isAdditionalOutbound === null && type == "additionalOutbound") {
    getOutboundType({
      dispatch,
      type: "csvValidator",
    });
    setIsAdditionalOutbound(1);
  }

  //when file droped or selected
  const onDropAccepted = async (e) => {
    //reset percent
    setIsProgressPercent(0);
    setIsUploadStatus(true);

    //get file type
    const file = e[0].name;
    const fileArr = file.split(".");
    const fileType = fileArr[1];

    //read file
    let fieldArray = field.map((obj) => obj.dataField);
    putCsvFieldJson({ dispatch, jsonArray: field });

    let jsonArray = null;
    let cleanArray = [];
    await setIsProgressStatus("Reading...");
    await setIsProgressFileName(file);
    await setIsProgressDisplay(true);
    if (fileType == "csv") {
      jsonArray = await csvReader(
        e,
        fieldArray,
        setIsProgressPercent,
        setIsProgressStatus
      );
      //console.log("csv", jsonArray);
    } else {
      jsonArray = await xlsReader(
        e,
        fieldArray,
        setIsProgressPercent,
        setIsProgressStatus,
        setIsJsonArray,
        dispatch
      );
      // console.log("xls", jsonArray);
    }
    console.log(jsonArray);
    if (jsonArray === false) {
      // resetCsvResist({ dispatch, resetStatus: 1, setIsResetCsvPersist });
      setModalShow(true);
      return 0;
    }

    await setIsProgressStatus("Validating Column Type...");
    await validateColumn({
      field,
      jsonArray,
      setIsProgressPercent,
      fileName: file,
      dispatch,
      warehouseArea: selectorWarehouseArea,
      warehouseUuid,
      outboundType: selectorOutboundType,
      setIsCheckingSchema,
    });
    await setIsProgressStatus("Validating PO SKU Code. . .");
  };

  const {
    getRootProps,
    getInputProps,
    isDragActive,
    isDragAccept,
    isDragReject,
    acceptedFiles,
    open,
  } = useDropzone({
    noClick: true,
    onDropAccepted,
    accept:
      //accept csv, xls, and xlsx
      "text/csv,application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
  });

  const style = useMemo(
    () => ({
      ...baseStyle,
      ...(isDragActive ? activeStyle : {}),
      ...(isDragAccept ? acceptStyle : {}),
      ...(isDragReject ? rejectStyle : {}),
    }),
    [isDragActive, isDragReject, isDragAccept]
  );

  const cancelCsv = () => {
    window.location.reload();
  };

  // Use Effect
  useEffect(() => {
    //setelah validasi kolom
    // validasi PO SKU Code
    // console.clear();
    validatePoSkuInfoId({
      dispatch,
      skuData: selectorDistinctPOSKUInfoId,
      status: isUploadStatus,
    });
  }, [selectorDistinctPOSKUInfoId]);

  useEffect(() => {
    //setelah validasi PO SKU Code
    //validasi SKU Number
    // console.clear();
    setIsProgressPercent(70);
    setIsProgressStatus("Validating SKU Numbers");
    validateSkuNumber({
      dispatch,
      skuNumberData: selectorDistinctSKUNumber,
      bulk: true,
      status: isUploadStatus,
    });
  }, [selectorSkuCodeValidated]);

  useEffect(() => {
    //setelah validasi sku number
    //re arrange data yang akan digunakan di table
    // console.clear();
    setIsProgressPercent(90);
    setIsProgressStatus("rearrange data. . .");
    cleaningData({
      poSkuInfoValidated: selectorSkuCodeValidated,
      poSkuNumberValidated: selectorSkuNumberValidated,
      csvData: selectorCsvData,
      transaction: transaction,
      setIsProgressPercent,
      dispatch,
      status: isUploadStatus,
      setIsProgressStatus,
      nextPage,
    });
  }, [selectorSkuNumberValidated]);

  useEffect(() => {
    if (isCheckingSchema === false) {
      console.log(isCheckingSchema);
      setModalShow2(true);
      setIsCheckingSchema(null);
    }
  }, [isCheckingSchema]);

  return (
    <div>
      <PopUp
        status={selectorStatus}
        onCloseFunction={() => {
          setModalShow(false);
          setIsProgressPercent(0);
          setIsProgressDisplay(false);
        }}
        statusDisplay={modalShow}
        title="Upload File Failed"
        cancelButtonTitle="Try Another File"
        actionButtonTitle={null}
        colorText="red"
        refreshOnCancel={false}
        msg1={
          <div>
            <div>
              Upload failed because your file has more than 2000 lines.{" "}
            </div>
            <div> Please make it less than 2000 lines. </div>
          </div>
        }
        msg2="Do you want to try another file?"
        cancelButtonClass="bg-red background"
        actionButtonClass=" "
      />

      <PopUp
        status={200}
        onCloseFunction={() => {
          setModalShow2(false);
          setIsProgressPercent(0);
          setIsProgressDisplay(false);
        }}
        actionButtonEvent={() => templateDownload()}
        statusDisplay={modalShow2}
        title="Different File Template"
        cancelButtonTitle="Cancel"
        actionButtonTitle="Download latest template"
        colorText="red"
        refreshOnCancel={false}
        msg1={
          <div>
            <div> Your uploaded file has a different template.</div>
          </div>
        }
        msg2="Do you want to download the latest file template ?"
        actionButtonClass="bg-green background"
        cancelButtonClass=""
      />

      <div className="w-100 d-flex flex-column p-3 mb-3 bg-white">
        <div
          className="d-flex flex-column justify-content-between"
          style={parentStyle}
        >
          <div className="FileUploader">
            <div
              className="d-flex flex-column justify-content-end"
              {...getRootProps({ style })}
            >
              <input {...getInputProps()} />
              <i
                style={{ fontSize: "90px" }}
                className="lineGrey iconU-upload"
              ></i>
              <b className="mt-2">Drag and Drop files here</b>
              <p className="mt-2 mb-2 emptyTextField"> Or </p>
              <button
                type="button"
                onClick={open}
                className="btn px-5 py-2 text-white bg-green LatoBold no-shadow"
              >
                Browse File
              </button>

              <div className="emptyTextField LatoBold mt-3 ">
                Support only CSV, XLS, XLSX file
              </div>
            </div>
          </div>
          <div className="downloadTemplate w-100 align-right">
            <span onClick={() => templateDownload()} className="float-right">
              Download the File Template
            </span>
          </div>
        </div>
      </div>

      <FileUploadProgress
        status={isProgressStatus}
        progressPercent={isProgressPercent}
        fileName={isProgressFileName}
        display={isProgressDisplay}
        cancelHandler={cancelCsv}
      />
    </div>
  );
};

export default FileUploader;
