import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { withRouter } from "react-router";
import FileUploader from "Component/FileUploader";
import ReactExport from "react-export-excel";
import "./index.scss";

const ExcelFile = ReactExport.ExcelFile;
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet;
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn;
const Upload = (props) => {
  const dispatch = useDispatch();
  const [isDisplayProgress, setIsDisplayProgress] = React.useState(false);
  const Field = [
    {
      dataField: "id", //field ini wajib ada untuk editable column
      text: "ID",
      editable: false,
      sort: true,
      hidden: true,
      type: "number",
    },
    { dataField: "areaName", text: "Area Source", type: "string", sort: true },
    { dataField: "areaNameDestination", text: "Area Destination", type: "string", sort: true },
    {
      dataField: "wmsPoSkuCode",
      text: "PO SKU Code",
      type: "number",
      sort: true,
    },
    {
      dataField: "skuNumber",
      text: "SKU Number",
      type: "number",
      sort: true,
    },
    {
      dataField: "expiryDateCol",
      text: "Expiry Date",
      type: "date_indo",
      sort: true,
    }, {
      dataField: "ripenessLevel",
      text: "Ripeness",
      type: "string",
      sort: true,
    },
    { dataField: "qty", text: "QTY", type: "number", sort: true }
  ];

  const dataSet1 = [
    {
      areaSource: "",
      areaDestination: "",
      po: "",
      skuNumber: "",
      ripenessLevel: "",
      inputWeight: ""
    },
  ];

  return (
    <div>
      {/* export this table to excel */}
      <ExcelFile
        element={
          //dont change id and style please
          <button id="download-template-xls" style={{ display: "none" }}>
            Download Data
          </button>
        }
        //file name without types
        filename="Movement_Template"
      >
        <ExcelSheet data={dataSet1} name="template">
          <ExcelColumn label="Area Source" value="areaSource" />
          <ExcelColumn label="Area Destination" value="areaDestination" />
          <ExcelColumn label="PO SKU Code" value="po" />
          <ExcelColumn label="SKU Number" value="skuNumber" />
          <ExcelColumn label="Expiry Date" value="skuNumber" />
          <ExcelColumn label="Ripeness Level" value="skuNumber" />
          <ExcelColumn label="Input Weight" value="inputWeight" />
        </ExcelSheet>
      </ExcelFile>
      {/* end export this table to excel */}

      <FileUploader
        nextPage={() =>
          props.history.push("/inventory-entry/movement/CSV-upload/review")
        }
        transaction={'movement'}
        field={Field}
        type="movement"
      />
    </div>
  );
};

export default withRouter(Upload);
