import React, { useEffect } from "react";
import "./index.scss";

const FileUploadProgress = ({
  status,
  progressPercent,
  fileName,
  display = false,
  cancelButton = null,
  cancelHandler,
}) => {
  useEffect(() => {
  }, [progressPercent, status]);
  return (
    <div
      className={
        "w-100 d-flex flex-column progressBar " +
        (!display ? "hiddenProgress" : null)
      }
    >
      <div className="pt-3 pb-0 pl-3 pr-3 bg-white border-all radius-all mb-3 mt-0">
        <div className="d-md-flex d-lg-flex mb-3 justify-content-between">
          <div className="uploadStatus w-100">
            <div className="row">
              <div className="col-md-7">
                <i className="iconU-sideBarInventoryEntry green"></i> File
                {fileName}
              </div>
              <div className="col-md-5">
                <p className="float-right" style={{ lineHeight: "30px", cursor: "pointer" }} onClick={() => { cancelHandler() }}>
                  Cancel
                </p>
              </div>
            </div>
            <div class="progress">
              <div
                class="progress-bar"
                role="progressbar"
                style={{ width: progressPercent + "%" }}
                aria-valuemin="0"
                aria-valuemax="100"
              ></div>
            </div>
            <div className="mt-2">
              <div className="emptyTextField inline">{status}</div>
              <div className="text inline font-weight-bold">
                {"  "}
                {progressPercent}%
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FileUploadProgress;
